body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: #a5a5a5;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

ul {
  list-style: none;
}

.logo {
  color: #08A6E4;
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
}

header {
  width: 100%;
  height: 70px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}

.logo {
  padding-left: 25px;
}

.menu {
  padding-right: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}
.menu__list {
  display: flex;
}
@media (max-width: 576px) {
  .menu__list {
    position: absolute;
    left: 0;
    top: 95px;
    flex-direction: column;
    background-color: #fff;
    width: 100%;
    padding-bottom: 20px;
    display: none;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  }
  .menu__list li {
    display: block;
    padding: 15px 20px;
  }
}
.menu__btn {
  display: none;
  position: absolute;
  opacity: 0;
  top: -9999999px;
  left: -9999999px;
}
@media (max-width: 576px) {
  .menu__btn:checked ~ .menu__list {
    display: flex;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
.menu__toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: #08A6E4;
  transition: 0.3s all ease-in-out;
}
.menu__toggle span:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .menu__toggle {
    display: none;
  }
}

.menu__list a {
  padding: 22px 16px;
  text-transform: uppercase;
  border-bottom: 3px solid transparent;
  margin-right: 20px;
}
@media (min-width: 1200px) {
  .menu__list a {
    padding-right: 100px;
  }
}
.menu__list a:last-child {
  margin-right: 0;
}
.menu__list a:hover {
  color: #000;
}
@media (min-width: 768px) {
  .menu__list a:hover:hover {
    padding: 22px 16px;
    border-color: #08A6E4;
  }
}
@media (min-width: 1200px) {
  .menu__list a:hover:hover {
    margin: 22px 84px 22px 0px;
  }
}